.listing-item
{
    display:    block;
}

.listing-item > img, .listing-item > video
{
    float:      left;
    margin-right: 8px;
    border-radius: 5px;
}

.listing-item > video {
    width:      100%;
    max-height: 480px;
}

.listing-item::after
{
    content:    '';
    display:    block;
    clear:      both;
}
